.fake {
    display: inline-block;
    width: 20px;
    height: 20px;
    border: 1px solid #000;
    border-radius: 3px;
    position: relative;
    cursor: pointer; }

.fake::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: block;
    width: 10px;
    height: 10px;
    background-color: #000;
    border-radius: 2px;
    opacity: 0;
    transition: .2s; }

.checkbox:checked ~ .fake::before {
    opacity: 1; }

